import { Disclosure } from '@headlessui/react';
import clsx from 'clsx';
import { forwardRef } from 'react';
import { FunctionalIcon } from '../icon/Icon';
import { Spacing } from '../spacing';
import { focusRing } from './../../tailwind/styles/focus';
import { AccordionProps } from './accordion.interface';

export const Accordion: React.FC<AccordionProps> = forwardRef<HTMLButtonElement, AccordionProps>(
  ({ items, ...props }, ref) => (
    <>
      {items.map((item, index) => {
        const { title, content, disabled } = item;
        return (
          <Disclosure key={`${title}-${index}`}>
            {({ open }) => {
              const iconClasses = clsx('zep-transition-transform', 'zep-duration-500', 'zep-shrink-0', {
                'zep-rotate-180': open,
              });
              return (
                <div
                  data-disabled={disabled}
                  className={clsx(
                    'zep-@container',
                    'first:zep-border-t-[0]',
                    'zep-border-t-1',
                    'last:zep-border-b-1',
                    'zep-border-greyscale-400',
                    '[&[data-disabled]]:zep-border-t-greyscale-400/40',
                    '[&[data-disabled]+*]:zep-border-t-greyscale-400/40',
                    '[&[data-disabled]]:last:zep-border-b-greyscale-400/40',
                    { '[&+*]:hover:!zep-border-t-neutral-dark-active': !disabled },
                    { 'hover:!zep-border-neutral-dark-active': !disabled },
                  )}
                >
                  <Disclosure.Button
                    disabled={disabled}
                    className={clsx(
                      'zep-typography-headlineXS-fluid-cqi',
                      'zep-w-full',
                      focusRing,
                      'focus-visible:zep-ring-offset-1',
                      'zep-py-0.75',
                      'zep-px-0.5',
                      'sm:zep-py-1.25',
                      'sm:zep-px-1',
                      { 'zep-cursor-not-allowed': disabled },
                      { 'zep-opacity-disabled': disabled },
                    )}
                    {...props}
                    ref={ref}
                    data-testid={`zep-accordion-button-${index}`}
                  >
                    <Spacing direction="row" justifyContent="between" gap="1.5" wrap="nowrap" className="zep-w-full">
                      <span className="zep-text-left">{title}</span>
                      <FunctionalIcon
                        className={clsx('zep-min-w-[24px] !zep-grow-0 zep-justify-end', iconClasses)}
                        name="chevron-mini-down"
                      />
                    </Spacing>
                  </Disclosure.Button>
                  <Disclosure.Panel
                    className={clsx(
                      { 'zep-hidden': !open },
                      'zep-text-typography-dark-100',
                      'zep-pt-0.75',
                      'zep-pb-2',
                      'zep-px-0.5',
                      'sm:zep-pt-1.25',
                      'sm:zep-pb-2.5',
                      'sm:zep-px-1',
                      'zep-typography-bodyText',
                    )}
                  >
                    {content}
                  </Disclosure.Panel>
                </div>
              );
            }}
          </Disclosure>
        );
      })}
    </>
  ),
);
